<template>
    <div>
        <div class="my-popups">
            <!-- <loader :is-visible="isLoading"></loader> -->
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full">
                            <h4 class="modal-title">Add Zone</h4>
                            <button type="button" class="close" v-on:click="$emit('Close')">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        
                         <div class="modal-body">
                            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"  v-on:OnSubmit="onSubmit"></dynamic-form>
                        </div>
                     </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props:{
        zoneEditData:{
            required:false
        }
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: 'Zone Name',
                            name: "txtZoneName",
                            value: "",
                            placeholder: "Enter Name",
                            validationRules: { "required": true, max: 100 },
                        }
                    ],
                },
                {
                    layoutType: "double",
                    Data: 
                    [
                      {
                        astype: "RadioField",
                        label: 'ZoneType',
                        name: "TypeFor",
                        value: "Locator",
                        placeholder: "",
                        config: {
                        options: [
                        {
                            name:'Locator Area',value:'Locator'
                        },
                        {
                            name:'High Profile',value:'High_Profile'
                        },
                        {
                            name:'Locator After Hours',value:'LocatorAfterHours'
                        },
                        {
                            name:'Yard',value:'Yard'
                        }
                    ],
                        showAddIcon: false,
                        onChange: this.onChangeRadio
                     },
                    validationRules: { "required": true},
                }]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",    
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 500 },          
                        },
                    ],
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    onClick: this.Save
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ],
            ListData: []
        }
    },
    created:function(){
        debugger
        var vm=this;
       // if(vm.zoneEditData.length>0){
            vm.FormSchema[0].Data[0].value=vm.zoneEditData.ZONE_NAME;
            vm.FormSchema[1].Data[0].value=vm.zoneEditData.ZONE_TYPE;
            vm.FormSchema[2].Data[0].value=vm.zoneEditData.ZONE_DESCRIPTION;
        //}
    },
    methods:{
        Close:function(){
            var vm=this;
            vm.$emit("Close");
        },
        Save:function() {
            debugger
            var vm = this;
            let obj={
                ZoneId:vm.zoneEditData==undefined?0:vm.zoneEditData.ZONE_ID,
                Name:vm.FormSchema[0].Data[0].value,
                ZoneType:vm.FormSchema[1].Data[0].value,
                Description:vm.FormSchema[2].Data[0].value
            }
            vm.$emit('SaveZoneData',obj);
        },
    }
}
</script>
   